export const categories = () => {
  let cat = deck.map((item, index) => item.tags).flat();
  return cat.filter((c, index) => c !== undefined && cat.indexOf(c) === index);
};

export const checkRelated = () => {
  let related = deck.map((item, index) => item.related).flat();
  let uniqueRelated = related.filter(
    (r, index) => r !== undefined && related.indexOf(r) === index
  );
  return uniqueRelated.filter(
    (x) => !deck.map((cardItem) => cardItem.text).includes(x)
  );
};
