const randomIndex = (max) => Math.floor(Math.random() * max);

function getRandomWithOneExclusion(lengthOfArray, indexToExclude) {
  var rand = null; //an integer
  while (rand === null || rand === indexToExclude) {
    rand = Math.round(Math.random() * (lengthOfArray - 1));
  }
  console.log("App.js __ returned getRandomWithExclusion", rand);
  return rand;
}

const getRandomWithExclude = (max, excludeArray) => {
  const randomNumber = Math.floor(
    Math.random() * (max + 1 - excludeArray.length)
  );
  const result =
    /*  max === 2 && excludeArray[0] === 0
      ? 1
      : max === 2 && excludeArray[0] === 1
      ? 0
      : */
    randomNumber +
    excludeArray
      .sort((a, b) => a - b)
      .reduce((acc, element) => {
        return randomNumber >= element - acc ? acc + 1 : acc;
      }, 0);

  console.log("App.js __ returned getRandomWithExclusion", result);
  return result;
};

