import { useState, useEffect } from "react";
//import { decks } from "./decks";

export default function Card(props) {
  const [topCard, setTopCard] = useState(props.card);
  const [bottomCard, setBottomCard] = useState(props.card);
  // const [currentIndex, setCurrentIndex] = useState(props.cardIndex);
  //const [prevIndex, storePrevIndex] = useState(0);
  const [textSide, setTextSide] = useState(true);
  const [animationState, setAnimationState] = useState("pause");

  useEffect(() => {
    topCard ? setBottomCard(topCard) : setBottomCard(props.card);
    setTopCard(props.card);
    //storePrevIndex(currentIndex);
    //setCurrentIndex(props.cardIndex);
    setAnimationState("play");
    // console.log("Card.js __ prevCard: ", bottomCard, " currentCard: ", topCard);
  }, [props.card]);

  useEffect(() => {
    console.log("Card.js __ AnimationState:", animationState);
    animationState === "play"
      ? setTimeout(() => {
          setAnimationState("pause");
          console.log("Card.js __ AnimationState:", animationState);
        }, 1000 * 1)
      : null; //1 sec
  }, [animationState]);

  /*if(left) {
    if(cardIndex - 1 >= 0) {
      this.setState({ cardIndex: cardIndex - 1 });
    }
  } else {
    if(cardIndex + 1 < this.props.cards.length) {
      this.setState({ cardIndex: cardIndex + 1 });
    }
  }
}
*/

  return (
    <div
      className="card-container"
      onClick={() => {
        setTextSide(!textSide);
        props.onCardChange(!textSide);
      }}
    >
      <div
        className={`card top ${animationState} ${textSide ? "front" : "back"}`}
        // style={{ animationName: animationState ? "swap-top" : "none" }}
      >
        <h1 className="label">
          {/*props.deck[currentIndex].text
            ? props.deck[currentIndex].text
          : "..."*/}
          {topCard.text ? topCard.text : "..."}
        </h1>
        <h2 className="description">
          {/*props.deck[currentIndex].hint ? props.deck[currentIndex].hint : ""*/}
          {topCard.hint ? topCard.hint : ""}
        </h2>
      </div>
      <div
        className={`card top ${animationState} ${!textSide ? "front" : "back"}`}
      >
        {
          /*props.deck[cardIndex].sign !== undefined
          ? props.deck[
            currentIndex
          ]*/
          props.card
            ? topCard.sign.map((item, index) => (
                <div
                  className="img"
                  key={`img${index}`}
                  style={{
                    backgroundImage: `url(${item})`,
                    width:
                      /*props.deck[currentIndex]*/ topCard.sign.length > 1
                        ? "50%"
                        : "100%",
                    height:
                      /*props.deck[currentIndex]*/ topCard.sign.length > 1
                        ? "50%"
                        : "100%"
                  }}
                />
              ))
            : null
        }
      </div>
      {/* ---PREV CARD--- */}
      <div
        className={`card bottom ${animationState} ${
          !textSide ? "front" : "back"
        }`}
      >
        <h1 className="label">
          {/*props.deck[prevIndex].text ? props.deck[prevIndex].text : "..."*/}
          {bottomCard.text ? bottomCard.text : "..."}
        </h1>
        <h2 className="description">
          {/*props.deck[prevIndex].hint ? props.deck[prevIndex].hint : ""*/}
          {bottomCard.hint ? bottomCard.hint : ""}
        </h2>
      </div>
      <div
        className={`card bottom ${animationState} ${
          textSide ? "front" : "back"
        }`}
      >
        {
          /*props.deck[prevIndex]*/ props.card
            ? bottomCard.sign.map((item, index) => (
                <div
                  className="img"
                  key={`img${index}`}
                  style={{
                    backgroundImage: `url(${item})`,
                    width:
                      /*props.deck[prevIndex]*/ bottomCard.sign.length > 1
                        ? "50%"
                        : "100%",
                    height:
                      /*props.deck[prevIndex]*/ bottomCard.sign.length > 1
                        ? "50%"
                        : "100%"
                  }}
                />
              ))
            : null
        }
      </div>
    </div>
  );
}

Card.defaultProps = {
  //  card: { text: "", hint: "", sign: [""] }
};
